/*================ Preview section ================*/
.hero {
  width: 100%;
  min-height: 810px;
  background-image: url(../img/mountains.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 225px;
  display: flex;
  justify-content: center;
  text-align: center; }
  .hero .hero__block {
    display: flex;
    flex-direction: column;
    align-items: center; }
    .hero .hero__block .hero__block-heading {
      color: #282828;
      font-family: "Exo Thin", sans-serif;
      font-size: 60px;
      font-weight: 400;
      line-height: 75px;
      letter-spacing: 2.4px;
      margin-bottom: 40px;
      max-width: 65%; }
      @media only screen and (max-width: 991px) {
        .hero .hero__block .hero__block-heading {
          font-size: 40px; } }
      @media only screen and (max-width: 767px) {
        .hero .hero__block .hero__block-heading {
          font-size: 30px; } }
    .hero .hero__block .hero__block-button-play {
      margin-bottom: 40px;
      width: 59px;
      border-radius: 50%;
      transition: 1s;
      cursor: pointer; }
      .hero .hero__block .hero__block-button-play:hover {
        transform: scale(1.2);
        box-shadow: 0px 0px 36px -9px #323969;
        border-radius: 50%; }
    .hero .hero__block .hero__block-button-try-now {
      color: #ffffff;
      background-color: #ff6d6d;
      font-family: "Exo Regular", sans-serif;
      font-size: 16px;
      text-transform: uppercase;
      letter-spacing: 3.2px;
      padding: 20px 50px;
      border-radius: 3px;
      margin-bottom: 20px;
      transition: 1s; }
      .hero .hero__block .hero__block-button-try-now:hover {
        box-shadow: 0px 0px 36px -9px #ff6d6d;
        border-radius: 8px;
        color: #000000; }
    .hero .hero__block .hero__block-note {
      opacity: 0.6;
      color: #282828;
      font-family: "Exo Regular", sans-serif;
      font-size: 14px;
      letter-spacing: 0.56px; }
      @media only screen and (max-width: 767px) {
        .hero .hero__block .hero__block-note {
          padding-bottom: 20px; } }

